:host {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
}

.erp-right-sidebar {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 350px;
  z-index: 1002;
  margin: 0 !important;
  padding: 0 !important;
  background: var(--bg-primary);
  border-left: 1px solid var(--border-color);
  box-shadow: -2px 0 8px var(--shadow);
  animation: slideInFromRight 0.3s ease-out;

  .main-menu,
  .expanded-panel {
    height: 100%;
    overflow-y: auto;
  }

  .panel-header {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px 20px;
    border-bottom: 1px solid var(--border-color);
    background: var(--bg-secondary);
    position: sticky;
    top: 0;
    z-index: 10;

    .header-title {
      display: flex;
      align-items: center;
      gap: 12px;
      flex: 1;

      .header-icon {
        font-size: 24px;
        line-height: 1;
      }
    }

    h3 {
      margin: 0;
      font-size: 18px;
      font-weight: 600;
      color: var(--text-primary);
    }

    .back-btn,
    .close-btn {
      background: none;
      border: none;
      font-size: 28px;
      cursor: pointer;
      color: var(--text-muted);
      padding: 0;
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.2s ease;
    }

    .back-btn:hover {
      color: var(--accent-primary);
      transform: translateX(-2px);
    }

    .close-btn:hover {
      color: var(--accent-danger);
      transform: rotate(90deg);
    }
  }

  // API 정보 카드
  .api-info-card {
    margin: 16px 12px;
    padding: 16px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
    display: flex;
    gap: 12px;
    align-items: flex-start;
    color: white;

    .info-icon {
      font-size: 28px;
      line-height: 1;
      flex-shrink: 0;
    }

    .info-content {
      flex: 1;

      .info-title {
        font-size: 16px;
        font-weight: 700;
        margin: 0 0 8px 0;
        color: white;
        letter-spacing: -0.3px;
      }

      .info-desc {
        font-size: 13px;
        margin: 0;
        color: rgba(255, 255, 255, 0.9);
        line-height: 1.5;
        font-weight: 400;
      }
    }
  }

  .category-list {
    padding: 12px;

    .category-item {
      display: flex;
      align-items: center;
      gap: 16px;
      padding: 16px;
      margin-bottom: 8px;
      background: var(--bg-secondary);
      border-radius: 12px;
      cursor: pointer;
      transition: all 0.2s ease;
      border: 1px solid transparent;

      &:hover {
        background: var(--bg-tertiary);
        border-color: var(--primary-color);
        transform: translateX(-4px);
        box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
      }

      .category-icon {
        font-size: 32px;
        width: 48px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--bg-tertiary);
        border-radius: 10px;
      }

      .category-content {
        flex: 1;

        .category-title {
          font-size: 15px;
          font-weight: 600;
          color: var(--text-primary);
          margin-bottom: 4px;
        }

        .category-count {
          font-size: 12px;
          color: var(--text-secondary);
          opacity: 0.7;
        }
      }

      .category-arrow {
        font-size: 24px;
        color: var(--text-secondary);
        opacity: 0.5;
        transition: all 0.2s ease;
      }

      &:hover .category-arrow {
        opacity: 1;
        transform: translateX(4px);
      }
    }
  }

  .panel-items {
    padding: 12px;

    .menu-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 14px 16px;
      margin-bottom: 8px;
      background: var(--bg-secondary);
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      border: 1px solid transparent;

      &:hover {
        background: var(--bg-tertiary);
        border-color: var(--primary-color);
        transform: translateX(-4px);
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
      }

      .item-content {
        flex: 1;

        .item-label {
          font-size: 14px;
          font-weight: 600;
          color: var(--text-primary);
          margin-bottom: 4px;
        }

        .item-description {
          font-size: 12px;
          color: var(--text-secondary);
          opacity: 0.8;
        }
      }

      .item-arrow {
        font-size: 20px;
        color: var(--text-secondary);
        opacity: 0.5;
        transition: all 0.2s ease;
      }

      &:hover .item-arrow {
        opacity: 1;
        transform: translateX(4px);
      }
    }
  }

  @keyframes slideInFromRight {
    from {
      transform: translateX(100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
}

// 반응형 디자인
@media (max-width: 1200px) {
  .erp-right-sidebar {
    width: 300px;
  }
}

@media (max-width: 992px) {
  .erp-right-sidebar {
    width: 320px;
    box-shadow: -4px 0 12px var(--shadow);
  }
}

@media (max-width: 768px) {
  .erp-right-sidebar {
    width: 90%;
    max-width: 350px;

    .panel-header {
      padding: 12px 16px;

      .header-title {
        gap: 10px;

        .header-icon {
          font-size: 20px;
        }
      }

      h3 {
        font-size: 16px;
      }

      .back-btn,
      .close-btn {
        width: 28px;
        height: 28px;
        font-size: 24px;
      }
    }

    .api-info-card {
      margin: 12px 8px;
      padding: 12px;
      gap: 10px;

      .info-icon {
        font-size: 24px;
      }

      .info-content {
        .info-title {
          font-size: 14px;
          margin-bottom: 6px;
        }

        .info-desc {
          font-size: 12px;
        }
      }
    }

    .category-list {
      padding: 8px;

      .category-item {
        padding: 12px;
        gap: 12px;

        .category-icon {
          font-size: 28px;
          width: 40px;
          height: 40px;
        }

        .category-content {
          .category-title {
            font-size: 14px;
          }

          .category-count {
            font-size: 11px;
          }
        }

        .category-arrow {
          font-size: 20px;
        }
      }
    }

    .panel-items {
      padding: 8px;

      .menu-item {
        padding: 12px;

        .item-content {
          .item-label {
            font-size: 13px;
          }

          .item-description {
            font-size: 11px;
          }
        }

        .item-arrow {
          font-size: 18px;
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .erp-right-sidebar {
    width: 95%;

    .api-info-card {
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 8px;

      .info-icon {
        font-size: 32px;
      }
    }

    .category-list {
      .category-item {
        padding: 10px;
        gap: 10px;

        .category-icon {
          font-size: 24px;
          width: 36px;
          height: 36px;
        }

      }
    }
  }
}

// API 테스트 패널 스타일
.api-test-panel {
  height: 100%;
  overflow-y: auto;

  .api-test-content {
    padding: 16px;
  }

  .section-title {
    font-size: 14px;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    gap: 6px;
  }

  .input-section {
    margin-bottom: 24px;

    .input-group {
      label {
        display: block;
        font-size: 13px;
        font-weight: 500;
        color: var(--text-secondary);
        margin-bottom: 8px;
      }

      .api-input {
        width: 100%;
        padding: 10px 12px;
        border: 1px solid var(--border-color);
        border-radius: 8px;
        font-size: 14px;
        background: var(--bg-primary);
        color: var(--text-primary);
        transition: all 0.3s ease;

        &:focus {
          outline: none;
          border-color: var(--accent-primary);
          box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
        }

        &::placeholder {
          color: var(--text-muted);
        }
      }

      .api-textarea {
        width: 100%;
        padding: 10px 12px;
        border: 1px solid var(--border-color);
        border-radius: 8px;
        font-size: 14px;
        background: var(--bg-primary);
        color: var(--text-primary);
        font-family: 'Courier New', monospace;
        resize: vertical;
        transition: all 0.3s ease;

        &:focus {
          outline: none;
          border-color: var(--accent-primary);
          box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
        }
      }

      .test-btn {
        width: 100%;
        padding: 12px;
        margin-top: 12px;
        border: none;
        border-radius: 8px;
        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
        color: white;
        font-size: 14px;
        font-weight: 600;
        cursor: pointer;
        transition: all 0.3s ease;

        &:hover:not(:disabled) {
          transform: translateY(-2px);
          box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
        }

        &:active:not(:disabled) {
          transform: translateY(0);
        }

        &:disabled {
          opacity: 0.6;
          cursor: not-allowed;
        }

        &.disabled {
          background: var(--bg-tertiary);
          color: var(--text-muted);
        }
      }

      .info-text {
        font-size: 13px;
        color: var(--text-secondary);
        margin-bottom: 12px;
        padding: 10px;
        background: var(--bg-secondary);
        border-radius: 8px;
        border-left: 3px solid var(--accent-primary);
      }

      .sync-options {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 12px;
        padding: 12px;
        background: var(--bg-secondary);
        border-radius: 8px;

        .checkbox-label {
          display: flex;
          align-items: center;
          gap: 8px;
          cursor: pointer;
          font-size: 13px;
          color: var(--text-primary);
          transition: all 0.2s ease;

          &:hover {
            color: var(--accent-primary);
          }

          input[type="checkbox"] {
            width: 18px;
            height: 18px;
            cursor: pointer;
            accent-color: var(--accent-primary);
          }
        }
      }
    }
  }

  .result-section {
    .result-message {
      padding: 12px;
      background: var(--bg-secondary);
      border-radius: 8px;
      font-size: 13px;
      color: var(--text-primary);
      margin-bottom: 12px;
      border-left: 3px solid var(--accent-primary);

      &.error {
        background: rgba(220, 53, 69, 0.1);
        border-left-color: #dc3545;
        color: #dc3545;
      }
    }

    .result-data {
      .data-card {
        background: var(--bg-secondary);
        border: 1px solid var(--border-color);
        border-radius: 8px;
        padding: 12px;
        margin-bottom: 12px;

        pre {
          margin: 0;
          font-size: 12px;
          color: var(--text-primary);
          white-space: pre-wrap;
          word-wrap: break-word;
          font-family: 'Courier New', monospace;
        }
      }
    }

    .total-count {
      font-size: 13px;
      color: var(--text-secondary);
      text-align: right;

      strong {
        color: var(--accent-primary);
        font-size: 16px;
      }
    }
  }
}
